.homepage-posts-wrapper {
  width: 90%;
  margin: 12rem auto;
  display: flex;
  flex-direction: column;
}

.homepage-post {
  width: 100%;
  height: 70vh;
  margin-bottom: 3rem;
  position: relative;
  border-radius: 5px;
  background-color: whitesmoke;
  box-shadow: 3px 3px 3px rgba(245, 10, 10, 0.5);
}

.homepage-post a {
  position: absolute;
  top: -1.3rem;
  left: 1rem;
  padding: 0.5rem;
  border: 2px solid rgb(233, 223, 79);
  border-radius: 5px;
  z-index: 2;
  background-color: rgb(233, 223, 79);
  text-transform: uppercase;
  text-decoration: none;
  transition: all ease 300ms;
}

.homepage-post a:hover {
    background-color: rgb(209, 203, 116);
}

.homepage-post-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  background-color: rosybrown;
}

.homepage-post-image {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10rem;
  font-family: 'Dancing Script', cursive;
  text-shadow: 2px 8px 4px rgba(10, 9, 9, 0.7);
}

.homepage-post-content div:nth-of-type(2) {
  font-size: 0.7rem;
  margin-left: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
}

.homepage-post-description {
  height: 30%;
  padding-left: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: rosybrown;
}

.homepage-post-description ul,
.homepage-post-description ol {
  margin-left: 1rem;
}


@media screen and (max-width: 769px) {
  .homepage-post-image{
    font-size: 7rem;
  }
}

@media screen and (max-width: 560px) {
  .homepage-post-image{
    font-size: 3rem;
  }

  .homepage-post-description {
    overflow: auto;
  }
  .homepage-post-description::-webkit-scrollbar {
    display: none;
  }
}